import axios from 'axios';
import { polyfill } from 'es6-promise';
polyfill();

/**
 * Save a filter.
 * @param {object} options The filter options.
 * @param {object} filters The filter values.
 * @param {object} target  The save button.
 * @param {string} baseurl The base URL.
 */
export default function save(options, filters, target, baseurl) {
	const spinner = document.querySelector('.saving-filter');
	spinner.classList.add('saving');

	const container = document.querySelector('.alm-filters');
	container.classList.add('saving');

	// REST API URL
	const url = alm_filters_localize.root + 'alm-filters/save/';

	axios
		.post(url, {
			options: JSON.stringify(options),
			filters: JSON.stringify(filters),
		})
		.then(function (response) {
			const { data = {} } = response;
			const { success = false, rebuild = false } = data;
			if (success) {
				setTimeout(function () {
					spinner.classList.remove('saving');
					if (rebuild) {
						window.location = `${baseurl}&auto_rebuild_index`;
						return;
					}
					window.location = `${baseurl}&filter_updated`;
				}, 500);
			} else {
				console.warn(response.data.msg);
				alert(response.data.msg);
				setTimeout(function () {
					spinner.classList.remove('saving');
					container.classList.remove('saving');
					target.disabled = false;
				}, 500);
			}
		})
		.catch(function (error) {
			console.warn(error);
		});
}
