/**
 * Fetch the status of a facet
 *
 * @param {string}  filter_id The filter ID
 * @param {Element} container The container to display the status
 */
export default function facetStatus(filter_id, container) {
	if (!filter_id || !container) {
		return;
	}

	// REST API URL
	const api_url = alm_filters_localize.root + 'alm-filters/facets/status/';

	const target = container.querySelector('#alm-facet-status--content');
	const loader = container.querySelector('#alm-facet-status--loader');
	const statusIcon = container.querySelector('.alm-status');
	const button = document.querySelector('#alm-rebuild-facet-index');
	const buttonCancel = document.querySelector('#alm-cancel-indexing');

	/**
	 * Get the status of the facet.
	 */
	function getStatus() {
		fetch(api_url, {
			method: 'POST',
			body: JSON.stringify({ filter_id }),
		})
			.then((data) => {
				return data.json();
			})
			.then((data) => {
				const { success, paged, pages, status = '', found_posts = 0, html = '' } = data;
				if (!success) {
					return;
				}

				if (target) {
					target.innerHTML = `${paged} / ${pages}`;
					target.innerHTML = html;
				}

				if (status === 'processing' || status === 'queued') {
					if (button) {
						button.classList.add('disabled'); // Disable the rebuild button.
					}

					if (buttonCancel) {
						buttonCancel.style.display = 'block'; // Show the cancel button.
					}

					// Continue to check the status, but wait for 2.5 seconds between checks.
					setTimeout(function () {
						getStatus();
					}, 2500);
				} else {
					// Index Complete.
					if (loader) {
						loader.style.display = 'none'; // Hide the loader.
					}

					if (statusIcon) {
						statusIcon.style.display = 'flex'; // Show the status icon.
					}

					if (button) {
						button.classList.remove('disabled'); // Show the rebuild button.
					}

					if (buttonCancel) {
						buttonCancel.style.display = 'none'; // Show the cancel button.
					}

					// Hide the admin notices.
					const notice = document.querySelectorAll('.notice.indexer-queued');
					if (notice) {
						notice.forEach((item) => {
							item.style.display = 'none';
						});
					}

					if (found_posts < 1) {
						container.dataset.state = 'failed';
					}
				}
			});
	}
	getStatus();
}
