import axios from 'axios';

/**
 * Render filter as code.
 *
 * @param {string} filter_id The ID of the filter to render.
 */
const renderfilter = (filter_id) => {
	const url = alm_filters_localize.root + 'alm-filters/renderfilter/';

	axios
		.post(url, {
			id: filter_id,
		})
		.then(function (response) {
			const { data = {} } = response;
			const { success, msg, code } = data;
			if (success) {
				let filter = code;
				filter = filter.replace(/:\s+/g, ' => ');
				// filter = filter.replace(/[[]/g, 'array(');
				filter = filter.replace(/[{]/g, '[');
				filter = filter.replace(/[}]/g, ']'); // }
				// filter = filter.replace(/[\\]/g, ''); // \
				// filter = filter.replace(/[]]/g, ')');
				// filter = filter.replace(']', ')'); // One more, not sure why
				// filter = filter.replace(']', ')'); // One more, not sure why

				const output = `$filter = ${filter};\necho alm_filters( $filter, 'YOUR_ALM_ID' );`;

				const pre = document.querySelector('.output');
				pre.textContent = output;
			} else {
				console.warn(msg);
			}
		})
		.catch(function (error) {
			console.warn(error);
		});
};

export default renderfilter;
