import Vue from 'vue/dist/vue.js';
import draggable from 'vuedraggable';
import { field_types, keys, meta_operators, meta_types, taxonomy_include_children, taxonomy_operators } from './modules/Objects';
import renderfilter from './modules/RenderFilter';
import save from './modules/Save';
import facetStatus from './modules/FacetStatus';
require('../frontend/helpers/polyfills');

/**
 * Function that initiates the building of filters.
 *
 * @since 1.0
 */
const filterBuilder = () => {
	let data = [
		{
			id: '',
			style: '',
			button_text: '',
			facets: false,
			facets_post_types: '',
			facets_hide_inactive: false,
			reset_button: false,
			reset_button_label: '',
		},
	];

	const filters = [];
	const create_btn_text = alm_filters_localize.create_filter;
	const update_btn_text = alm_filters_localize.update_filter;

	Vue.config.productionTip = false;

	// Filter Template component
	Vue.component('filter-template', {
		template: '#filterTemplate',
		props: [
			'data',
			'filter_id',
			'filters',
			'filter',
			'keys',
			'field_types',
			'meta_types',
			'meta_operators',
			'taxonomy_operators',
			'taxonomy_include_children',
			'index',
		],

		beforeMount: function () {
			const id = data && data[0] ? data[0].id : ''; // Get ID from data.
			if (id && this.filters && this.filters.length > 0) {
				const self = this;
				this.filters.forEach(function (filter, index) {
					self.buildHookName(filter, index);
				});
			}
		},

		methods: {
			// Filter onchange
			filterChange: function (event) {
				let target = event.target;
				let id = target.dataset.id;
				let index = target.dataset.index;

				// Set the value.
				if (target.type === 'checkbox') {
					this.filters[index][id] = target.checked; // Checkbox
				} else {
					this.filters[index][id] = target.value; // Standard
				}

				// Search.
				// If `key` is search, select textfield by default.
				if (id === 'key' && target.value === 'search') {
					this.filters[index]['field_type'] = 'text';
				}

				// Posts Per Page.
				// If `key` is per_page, select the `select` fieldtype by default.
				if (id === 'key' && target.value === 'per_page') {
					this.filters[index]['field_type'] = 'select';
				}

				// Star Rating.
				// If `field_type` is star_rating, set meta fields.
				if (id === 'field_type' && target.value === 'star_rating') {
					this.filters[index]['key'] = 'meta';
					this.filters[index]['meta_operator'] = '>=';
					this.filters[index]['meta_type'] = 'NUMERIC';
					this.filters[index]['star_rating_min'] = '1';
					this.filters[index]['star_rating_max'] = '5';
				}

				// Scroll user to top of filter block.
				if (id === 'field_type' || id === 'key') {
					this.scrollToElement(target);
				}

				// Build the filter hook name for users.
				this.buildHookName(this.filters[index], index);
			},

			/**
			 * Get the value for the hook/filter.
			 *
			 * @param {object} filter The filter object.
			 * @param {number} index The index key for all filters.
			 */
			buildHookName: function (filter, index) {
				const id = data && data[0] ? data[0].id : ''; // Get ID from data.
				if (!id) {
					this.filters[index].hookname = '';
					return;
				}
				let key = filter.key;
				if (key === 'taxonomy') {
					key = filter.taxonomy;
				}
				if (key === 'meta') {
					key = filter.meta_key;
				}
				if (!id || !key || undefined === key) {
					this.filters[index].hookname = '';
					return;
				}
				this.filters[index].hookname = `alm_filters_${id}_${key}`;
			},

			// Checkbox Filter onchange
			filterCheckboxChange: function (event) {
				let target = event.target;
				let id = target.dataset.id;
				let index = target.dataset.index;

				// Set the value
				this.filters[index][id] = target.checked ? 'true' : '';
			},

			// Custom Value Change
			customValueChange: function (event) {
				let parent = event.target.parentNode.parentNode.parentNode; //.value-fields-wrap
				let id = parent.dataset.id;
				let index = parent.dataset.index;
				let key = parent.dataset.key;

				let customValueRows = parent.querySelectorAll('.value-fields-wrap--field'); // Get all custom value rows

				let customValueArray = [];

				// Loop each row
				[...customValueRows].forEach((item, e) => {
					let label = item.querySelector('.values-label').value; // Label val
					let value;

					if (key === 'sort') {
						// Sort key
						value = item.querySelector('.values-sortvalue').value; // Sort Value val*
					} else {
						// Regular
						value = item.querySelector('.values-value').value; // Value val*
					}

					let array = {};
					array.label = label;
					array.value = value;

					customValueArray.push(array);
				});

				this.filters[index][id] = customValueArray;
			},

			// Add Custom Value
			addCustomValue: function (event) {
				let target = event.target;
				let id = target.dataset.id; // values
				let index = target.dataset.index;

				let customValueArray = this.filters[index][id];

				// Create emepty array
				let array = {
					label: '',
					value: '',
				};

				customValueArray.push(array);
				this.filters[index][id] = customValueArray;

				// Set focus on new custom values
				setTimeout(function () {
					let newElement = target.parentNode.parentNode.querySelector('.value-fields-wrap--field:last-child');
					if (newElement) {
						newElement.querySelector('label:first-child input').focus();
					}
				}, 100);
			},

			// Move Custom Values Up/down
			customValueMove: function (event) {
				let target = event.currentTarget;
				if (target.classList.contains('disabled')) {
					return false;
				}
				let id = target.dataset.id; // values
				let index = target.dataset.index; // Current value index
				let target_index = '';
				let direction = target.dataset.direction; // Current value index

				// Get current filter index from parent node
				let parent = event.currentTarget.parentNode.parentNode.parentNode;
				let parentIndex = parent.dataset.index; // Current filter index
				let valueArray = this.filters[parentIndex][id];

				let old_index = parseInt(index);

				let current = valueArray[old_index]; // Assign current item to new var
				valueArray.splice(old_index, 1); // Remove from array

				if (direction === 'up' && index > 0) {
					target_index = old_index - 1;
				}
				if (direction === 'down') {
					target_index = old_index + 1;
				}
				valueArray.splice(target_index, 0, current);
			},

			// Remove Custom Value
			removeCustomValue: function (event) {
				let target = event.currentTarget;
				let id = target.dataset.id; // values
				let index = target.dataset.index; // Current value index

				// Get current filter index from parent node
				let parent = event.currentTarget.parentNode.parentNode.parentNode;
				let parentIndex = parent.dataset.index; // Current filter index

				let valueArray = this.filters[parentIndex][id];
				let valueLength = valueArray.length;
				if (valueLength > 0) {
					valueArray.splice(index, 1); // Remove array item
					this.filters[parentIndex][id] = valueArray;
				}
			},

			// Remove Filter
			removeFilter: function (event) {
				const self = this;
				const target = event.currentTarget;
				if (target.classList.contains('disabled')) {
					return;
				}
				const index = parseInt(target.dataset.index);
				const parent = target.parentNode.parentNode;
				parent.classList.add('removing');
				// Delay for delete fade.
				setTimeout(function () {
					if (!target.classList.contains('disabled')) {
						self.filters.splice(index, 1); // Remove array item
					}
				}, 500);
			},

			/**
			 * Get the current selected option.
			 *
			 * @param {string} current  Current value.
			 * @param {string} test     Value to test.
			 * @param {string} fallback The fallback value to select.
			 * @return {string}
			 */
			isActive: function (current, test, fallback) {
				const selected = current === test || (test === '' && current === fallback) ? 'selected' : '';
				return selected;
			},

			/**
			 * Are facets currently active.
			 *
			 * @return {boolean}
			 */
			facetsActive: function () {
				return data[0] && data[0].facets;
			},

			/**
			 * Show/Hide `Result Count` option based on key selection.
			 *
			 * @param {string} key Query parameter.
			 */
			checkShowCount: function (key) {
				const allowed = ['taxonomy', 'category', 'category__and', 'tag', 'tag__and', 'year', 'month', 'day'];
				if (data[0] && data[0].facets) {
					allowed.push('meta', 'author', 'post_type'); // Add these keys for facets.
				}
				const found = allowed.indexOf(key);
				return found !== -1;
			},

			/**
			 * Show/Hide `Query Option` filters based on key selection.
			 *
			 * @param {string} key Query parameter.
			 */
			checkQueryOptions: function (key) {
				const array = ['author', 'taxonomy', 'category', 'category__and', 'tag', 'tag__and', 'meta'];
				const found = array.indexOf(key);
				return found !== -1;
			},

			// Show/Hide `Exclude` filter based on key selection
			checkExclude: function (theKey, index) {
				const array = ['', 'sort', 'meta', 'order', 'orderby', 'year', 'month', 'day', 'search', 'post_type'];
				const found = array.indexOf(theKey);

				// If key is found in array, hide the field.
				if (found !== -1) {
					this.filters[index].exclude = ''; // Set exclude
					return false;
				}

				return true;
			},

			// Show/Hide `Label` filter based on field_type selection
			checkLabel: function (theFieldType, index) {
				let show_filter_field = true;
				const hideArray = ['radio', 'checkbox', 'star_rating'];

				// If fieldtype is found in array, hide the field
				if (hideArray.indexOf(theFieldType) !== -1) {
					this.filters[index].label = ''; // Set exclude
					show_filter_field = false;
				}

				return show_filter_field;
			},

			/**
			 * Open/Close an individual filter group.
			 *
			 * @param {MouseEvent} event The event object.
			 */
			toggleFilterGroup: function (event) {
				const button = event.currentTarget;
				const container = button.closest('.alm-filter--wrapper');
				const wrap = container.querySelector('.collapsible');

				if (button.classList.contains('closed')) {
					button.classList.remove('closed');
					button.setAttribute('title', alm_filters_localize.close_filter);
					button.setAttribute('aria-title', alm_filters_localize.close_filter);
					wrap.classList.remove('closed');
				} else {
					button.classList.add('closed');
					button.setAttribute('title', alm_filters_localize.expand_filter);
					button.setAttribute('aria-title', alm_filters_localize.expand_filter);
					wrap.classList.add('closed');
				}
			},

			// Open/Close instructions.
			toggleInstructions: function (event) {
				const target = event.currentTarget;
				const parent = target.parentNode;
				const instructions = parent.querySelector('.alm-instructions--copy');
				if (instructions) {
					if (instructions.classList.contains('open')) {
						instructions.classList.remove('open');
					} else {
						instructions.classList.add('open');
					}
				}
			},

			// Open/Close help text.
			toggleHelp: function (event) {
				const button = event.currentTarget;
				const parent = button.parentNode.parentNode.parentNode;
				const help = parent.querySelector('.alm-filter--row-help');
				if (!help) {
					return false;
				}
				if (help.classList.contains('open')) {
					help.classList.remove('open');
					button.setAttribute('aria-expanded', false);
					help.setAttribute('aria-hidden', true);
					button.focus({ preventScroll: true });
				} else {
					help.classList.add('open');
					button.setAttribute('aria-expanded', true);
					help.setAttribute('aria-hidden', false);
					help.focus({ preventScroll: true });
				}
			},

			// Copy content to clipboard.
			copyContent(event) {
				const target = event.currentTarget;
				const content = target.dataset.copy;
				if (content) {
					navigator.clipboard.writeText(content);
					target.classList.add('copied');
					setTimeout(() => {
						target.classList.remove('copied');
					}, 2000);
				}
			},

			// Scroll to element
			scrollToElement: function (element) {
				const parent = element.closest('.alm-filter--wrapper');
				const position = this.getOffset(parent);
				if (!position) {
					return false;
				}
				window.scrollTo({
					top: position.top - 128,
					behavior: 'smooth',
				});
			},

			// Get browser offset
			getOffset: function (el = null) {
				if (!el) {
					return false;
				}
				const rect = el.getBoundingClientRect();
				const scrollLeft = window.scrollX || document.documentElement.scrollLeft;
				const scrollTop = window.scrollY || document.documentElement.scrollTop;
				return { top: rect.top + scrollTop, left: rect.left + scrollLeft };
			},
		},
	});

	new Vue({
		el: '#app',

		components: {
			draggable,
		},

		data() {
			return {
				data: data,
				filters: filters,
				keys: keys,
				field_types: field_types,
				meta_types: meta_types,
				meta_operators: meta_operators,
				taxonomy_operators: taxonomy_operators,
				taxonomy_include_children: taxonomy_include_children,
				create_btn_text: create_btn_text,
				update_btn_text: update_btn_text,
			};
		},

		watch: {
			data: function () {
				// nothing yet
			},
			filters: function () {
				// nothing yet
			},
		},

		methods: {
			// Draggable onEnd [https://github.com/SortableJS/Vue.Draggable#events]
			onEnd: function () {
				this.setFilterOrder();
			},

			/**
			 * Set default filter options.
			 *
			 * @return {object} The default filter options as an object.
			 */
			defaults: function () {
				return {
					uniqueid: this.getUniqueID(),
					order: this.filters.length + 1,
					key: '',
					field_type: '',
					author_role: '',
					meta_key: '',
					meta_operator: 'IN',
					meta_type: 'CHAR',
					taxonomy: '',
					taxonomy_operator: 'IN',
					taxonomy_include_children: 'true',
					values: [],
					exclude: '',
					label: '',
					placeholder: '',
					default_select_option: '',
					default_value: '',
					selected_value: '',
					title: '',
					description: '',
					button_label: '',
					classes: '',
					show_count: false,
					checkbox_limit: '',
					checkbox_limit_label_open: '',
					checkbox_limit_label_close: '',
					checkbox_toggle: '',
					checkbox_toggle_label: '',
					section_toggle: false,
					section_toggle_status: '',
				};
			},

			// Set the order of filters
			setFilterOrder: function () {
				let rows = document.querySelectorAll('.alm-filter--wrapper');
				[...rows].forEach((row, i) => {
					let target = row.querySelector('input.filter-order');
					let id = target.dataset.id;
					let index = target.dataset.index;
					let oldindex = parseInt(target.dataset.oldindex);

					this.filters[oldindex][id] = target.value;
					target.dataset.oldindex = index;
				});
			},

			// Expand all filters
			expandFilters: function () {
				const rows = document.querySelectorAll('.alm-filter--wrapper');
				[...rows].forEach((row) => {
					const collapsible = row.querySelector('.collapsible');
					const collapsible_controls = row.querySelector('.collapsible-controls button');
					collapsible_controls.classList.remove('closed');
					collapsible.classList.remove('closed');
				});
				localStorage.setItem('alm_filters_expanded', 'true');
			},

			// Collapse all filters
			collapseFilters: function () {
				const rows = document.querySelectorAll('.alm-filter--wrapper');
				[...rows].forEach((row) => {
					const collapsible = row.querySelector('.collapsible');
					const collapsible_controls = row.querySelector('.collapsible-controls button');
					collapsible_controls.classList.add('closed');
					collapsible.classList.add('closed');
				});
				localStorage.setItem('alm_filters_expanded', 'false');
			},

			// Options onchange
			optionsChange: function (event) {
				const target = event.target;
				const id = target.dataset.id;

				if (target.type === 'checkbox') {
					// Checkbox
					this.data[0][id] = target.checked;
				} else if (target.type === 'select-multiple') {
					const selected = [];
					[...target.options].forEach((option) => {
						if (option.selected) {
							selected.push(option.value);
						}
					});
					this.data[0][id] = selected;
				} else {
					this.data[0][id] = target.value; // Set Value
				}
			},

			// Add an empty filter block with defaults.
			addFilter: function () {
				this.filters.push(this.defaults());

				// Set focus on new filter group
				setTimeout(function () {
					const newElement = document.querySelector('.alm-filter--wrapper:last-child');
					newElement.focus();
				}, 150);
			},

			// Delete a filter
			deleteFilter: function (event) {
				let target = event.currentTarget;
				let id = target.dataset.id;
				if (id) {
					let confirm_delete = confirm(alm_filters_localize.delete_filter + ' ' + id + '?');
					if (confirm_delete == true) {
						window.location = alm_filters_localize.base_url + '&delete_filter=' + id;
					} else {
						event.preventDefault();
						return false;
					}
				}
			},

			// Save the filter
			saveFilter: function (event) {
				const target = event.target;
				target.disabled = true; // disable button

				const baseurl = target.dataset.baseurl;
				const url = `${baseurl}&filter=${this.data[0].id}`; // set baseurl

				save(this.data, this.filters, target, url);
			},

			// Prevent empty saving
			isEmpty: function () {
				// Check Key and Style fields
				let is_empty = this.data[0].id.trim() === '' || this.data[0].style.trim() === '' ? true : false;

				return is_empty;
			},

			// Restrict the input keys for the filter ID
			restrictIDChars: function (event) {
				const charCode = event.which ? event.which : event.keyCode;

				// Chars to exclude
				const excludeArray = [32, 45, 61, 93, 46, 44, 59, 47, 43, 125, 123, 34, 58, 59, 39, 91];
				const found = excludeArray.indexOf(charCode);

				if (found !== -1) {
					// if found, dont return
					event.preventDefault();
				} else {
					return true;
				}
			},

			// Render filter code in modal
			renderFilterCode: function (code) {
				renderfilter(code);
			},

			// Fade Element Out
			fadeOut: function (event) {
				var s = event.style;
				s.opacity = 1;
				(function fade() {
					(s.opacity -= 0.15) < 0 ? (s.display = 'none') : setTimeout(fade, 40);
				})();
			},

			// Parse the inital filter
			parseFilter: function (obj) {
				obj = JSON.parse(obj);

				this.data[0].id = obj['id'];
				this.data[0].style = obj['style'];
				this.data[0].button_text = obj['button_text'];
				this.data[0].facets = obj['facets'];
				this.data[0].facets_post_types = obj['facets_post_types'];
				this.data[0].facets_hide_inactive = obj['facets_hide_inactive'];
				this.data[0].reset_button = obj['reset_button'];
				this.data[0].reset_button_label = obj['reset_button_label'];

				// Loop the filters and push into filter array
				const the_filters = obj['filters'];

				for (let index in the_filters) {
					// Merge current filter defaults and current filter into new object.
					const filterObj = Object.assign({}, this.defaults(), the_filters[index]);
					//const filterObj = the_filters[index];

					// Auto set order on load.
					filterObj.order = parseInt(index) + 1;

					// Check for undefined Custom Values obj.
					// If undefined, create an empty array object otherwise we can't append arrays.
					if (typeof the_filters[index].values === 'undefined') {
						the_filters[index].values = [];
					}

					// Set uniqueid (Vue Key) if not defined
					if (typeof the_filters[index].uniqueid === 'undefined') {
						the_filters[index].uniqueid = this.getUniqueID();
					}

					filters.push(filterObj);
				}
			},

			// Generate a Unique ID
			getUniqueID: function () {
				return Math.random().toString(36).substr(2, 9);
			},

			// Show Generated PHP Modal
			showOutput: function () {
				const popup = document.querySelector('#alm-filter-pop-up');
				if (popup.classList.contains('show')) {
					popup.classList.remove('show');
				} else {
					popup.classList.add('show');
				}
			},

			// Close PHP Output Modal
			closeModal: function () {
				const popup = document.querySelector('#alm-filter-pop-up');
				popup.classList.remove('show');
			},
		},

		mounted: function () {
			const self = this;

			// Fadeout the deleted alert message
			const deleted = document.getElementById('filter-deleted');
			if (deleted) {
				setTimeout(function () {
					self.fadeOut(deleted);
				}, 2000);
			}

			// Detect escape key listener.
			window.addEventListener('keydown', function (e) {
				const { key = null } = e;
				if (key && key.toLowerCase() === 'escape') {
					self.closeModal(); // Close modal.
				}
			});

			// Render filter as PHP.
			if (alm_filter_id) {
				this.renderFilterCode(alm_filter_id);
			}

			// Parse the filters for display.
			if (alm_filters) {
				// Viewing/Editing a filter, parse JSON.
				this.parseFilter(alm_filters);
			} else {
				// Create a filter on page load.
				setTimeout(function () {
					// Delay to allow page to fully render.
					self.addFilter();
				}, 500);
			}

			/**
			 * Close filters if set via localstorage.
			 *
			 * @see https://v2.vuejs.org/v2/api/#mounted
			 */
			this.$nextTick(function () {
				const expanded = localStorage.getItem('alm_filters_expanded');
				if (expanded === 'false') {
					this.collapseFilters();
				}
			});
		},
	});
};

/**
 * Restict ID chars in input.
 */
window.restrictIDChars = function (event) {
	const charCode = event.which ? event.which : event.keyCode;
	const excludeArray = [32, 45, 61, 93, 46, 44, 59, 47, 43, 125, 123, 34, 58, 59, 39, 91]; // Chars to exclude
	const found = excludeArray.indexOf(charCode);

	if (found !== -1) {
		// if found, dont return
		event.preventDefault();
	} else {
		return true;
	}
};

// Start the App
const filter_app = document.getElementById('app');
if (filter_app) {
	filterBuilder();
}

/**
 * Toggle checkbox for filter exports.
 */
const toggleExportBtn = document.getElementById('toggle-all-filters');
if (toggleExportBtn) {
	const exportFilters = document.querySelectorAll('input[name="filter_keys[]"');
	toggleExportBtn.addEventListener('change', function (e) {
		const target = e.target;
		for (let i = 0; i < exportFilters.length; ++i) {
			if (target.checked) {
				exportFilters[i].checked = true;
			} else {
				exportFilters[i].checked = false;
			}
		}
	});
}

/**
 * Duplicate filter functionality.
 */
const duplicateFilterButtons = document.querySelectorAll('.row-actions .duplicate-filter');
if (duplicateFilterButtons) {
	duplicateFilterButtons.forEach((button) => {
		button.addEventListener('click', function (e) {
			e.preventDefault();
			const target = document.querySelector('#alm-filter-pop-up');
			if (target) {
				target.classList.add('show');
				const form = target.querySelector('form');
				const dup_id = form.querySelector("input[name='duplicate_filter']");
				dup_id.value = button.dataset.id;
				const input = form.querySelector("input[name='filter_id']");
				input.value = button.dataset.id + '_copy';
				setTimeout(function () {
					input.focus();
					const val = this.input.value; // store the value of the element
					input.value = ''; //clear the value of the element
					input.value = val; //set that value back.
				}, 100);
			}
		});
	});
	const closeButton = document.querySelector('#alm-filter-pop-up .dup-form-close');
	if (closeButton) {
		closeButton.addEventListener('click', function () {
			const target = document.querySelector('#alm-filter-pop-up');
			if (target) {
				target.classList.remove('show');
			}
		});
	}
	// Add escape handler.
	document.addEventListener('keydown', (event) => {
		if (event.key === 'Escape') {
			closeButton.click();
		}
	});
}

/**
 * Save Filters with cmd + s and ctrl + s
 *
 * @since 2.2.0
 */
document.addEventListener('keydown', function (e) {
	if ((e.ctrlKey || e.metaKey) && e.key === 's') {
		e.preventDefault();
		const saveButton = document.querySelector('.alm-filter--actions button.save-filter');
		if (saveButton) {
			saveButton.click();
		}
	}
});

/**
 * Document Ready.
 */
document.addEventListener('DOMContentLoaded', function () {
	/**
	 * Remove `filter_updated=true` from URL after 1 second.
	 */
	const url = window.location.href;
	if (history && url && (url.includes('&filter_updated') || url.includes('&rebuild_index') || url.includes('&auto_rebuild_index'))) {
		const new_url = url.replace('&filter_updated', '').replace('&rebuild_index', '').replace('&auto_rebuild_index', '');
		setTimeout(function () {
			history.replaceState(null, null, new_url);
		}, 500);
	}

	/**
	 * Initiate Facet Status.
	 */
	const facetStatusDiv = document.querySelector('#alm-facet-status');
	const rebuildButtons = document.querySelectorAll('#alm-rebuild-facet-index, #alm-rebuild-facet-index-notice');
	const cancelButton = document.querySelector('#alm-cancel-indexing');

	if (facetStatusDiv) {
		const filter_id = facetStatusDiv.dataset.filterId;
		if (!filter_id) {
			return;
		}
		facetStatus(filter_id, facetStatusDiv);

		// Facet rebuild buttons.
		if (rebuildButtons) {
			rebuildButtons.forEach((button) => {
				button.addEventListener('click', function (e) {
					if (confirm(alm_filters_localize.rebuild_index) !== true) {
						e.preventDefault();
					}
				});
			});
		}

		// Facet rebuild buttons.
		if (cancelButton) {
			cancelButton.addEventListener('click', function (e) {
				if (confirm(alm_filters_localize.cancel_index) !== true) {
					e.preventDefault();
				}
			});
		}
	}

	/**
	 * Delete Filter Buttons.
	 */
	const deleteLinks = document.querySelectorAll('.filter-listing--main a.delete-filter');
	if (deleteLinks) {
		deleteLinks.forEach((link) => {
			link.addEventListener('click', function (event) {
				const id = event.target.dataset.name;
				if (!confirm(alm_filters_localize.delete_filter + ' ' + id + '?')) {
					event.preventDefault();
				}
			});
		});
	}
});
